<template>
  <span>
    <b>{{spelling.spelling}}</b>
    <span class="ffkk">
      <sup>[source]</sup>
      <span class="ffkktt" v-html="articulateSource(spelling.bibtex_citation)"/>
    </span>
  </span>
</template>

<script>
const Cite = require('citation-js'); // TODO: see if you can just import function

export default {
  name: 'Spelling',
  props: {
    spelling: Object
  },
  methods: {
    articulateSource: function (citation) {
      let returnCitation;
      try {
        returnCitation = Cite(citation).format('bibliography', {
          format: 'html',
          template: 'harvard',
          lang: 'en-UK'
        });
      } catch (error) {
        returnCitation = citation;
      }
      // return ['This spelling has been cited from: ', returnCitation].join(' ');
      return returnCitation;
    },
    copyBibtex: async function (input) {
      await navigator.clipboard.writeText(input);
    },
  }
}
</script>

